
"use client";

import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { timelineEventsData } from "@/data/timelineEvents";
import type { TimelineEvent, LucideIconComponent } from "@/types/timeline";
import { useTranslations } from "@/hooks/useTranslations";
import * as LucideIcons from 'lucide-react'; // Import all icons
import { cn } from "@/lib/utils";

// Helper function to get icon component by name
const getIcon = (iconName?: keyof typeof LucideIcons): LucideIconComponent | null => {
  if (!iconName) return null;
  const IconComponent = LucideIcons[iconName] as LucideIconComponent | undefined;
  return IconComponent || LucideIcons.CalendarDays; // Fallback icon
};

export default function TimelineSection() {
  const t = useTranslations();

  // Access nested translation object safely
  const timelineTranslations = t.timeline || {};
  const eventsTranslations = timelineTranslations.events || {};

  return (
    <section id="timeline" className="py-16">
      <div className="text-center mb-12 md:mb-16">
        <h2 className="text-3xl md:text-4xl font-bold text-primary">
          {timelineTranslations.title || "My Journey"}
        </h2>
        <p className="text-md text-muted-foreground mt-2">
          {timelineTranslations.subtitle || "A brief history of my milestones and experiences."}
        </p>
      </div>

      <div className="relative max-w-3xl mx-auto">
        {/* Central line for the timeline (optional visual element) */}
        <div className="absolute left-1/2 top-0 bottom-0 w-1 bg-border/50 transform -translate-x-1/2 hidden md:block" aria-hidden="true"></div>

        {timelineEventsData.map((event, index) => {
          const IconComponent = getIcon(event.iconName);
          // Resolve translations for each event
          const eventLocaleData = eventsTranslations[event.id as keyof typeof eventsTranslations] || {};
          const dateText = eventLocaleData.date || event.dateKey;
          const titleText = eventLocaleData.title || event.titleKey;
          const descriptionText = eventLocaleData.description || event.descriptionKey;
          
          const isEven = index % 2 === 0;

          return (
            <div 
              key={event.id} 
              className={cn(
                "mb-8 md:mb-12 flex md:items-center w-full",
                isEven ? "md:flex-row-reverse" : "md:flex-row"
              )}
            >
              <div className="hidden md:flex md:w-1/2">
                {/* Spacer for alternating layout */}
              </div>

              <div className="relative w-full md:w-1/2 md:px-6">
                {/* Icon and connecting dot */}
                <div className={cn(
                    "absolute top-1 hidden md:block transform -translate-y-1/2",
                    isEven ? "right-full -mr-[1.30rem]" : "left-full -ml-[1.30rem]" 
                  )}
                >
                  <div className={cn("w-8 h-8 rounded-full flex items-center justify-center text-primary-foreground", event.iconBgColor || 'bg-primary')}>
                    {IconComponent && <IconComponent className="w-5 h-5" />}
                  </div>
                </div>
                
                {/* Mobile Icon (Optional: if you want icons on mobile too) */}
                 <div className="md:hidden flex items-center mb-2">
                   <div className={cn("w-8 h-8 rounded-full flex items-center justify-center text-primary-foreground shrink-0 mr-3", event.iconBgColor || 'bg-primary')}>
                     {IconComponent && <IconComponent className="w-5 h-5" />}
                   </div>
                   <p className="text-sm font-semibold text-muted-foreground">{dateText}</p>
                 </div>


                <Card className="shadow-lg hover:shadow-xl transition-shadow duration-300 bg-card/80 backdrop-blur-sm w-full">
                  <CardHeader className="pb-3">
                    <p className="text-xs text-muted-foreground hidden md:block">{dateText}</p>
                    <CardTitle className="text-lg md:text-xl text-primary">{titleText}</CardTitle>
                  </CardHeader>
                  <CardContent className="pb-4">
                    <CardDescription className="text-sm text-card-foreground/80 mb-3">
                      {descriptionText}
                    </CardDescription>
                    {event.tags && event.tags.length > 0 && (
                      <div className="flex flex-wrap gap-2">
                        {event.tags.map((tag) => (
                          <Badge 
                            key={tag} 
                            variant="secondary" 
                            className="text-xs px-2 py-0.5 bg-secondary/20 border border-secondary/30 text-secondary-foreground hover:bg-secondary/30 hover:border-secondary/50 transition-colors"
                          >
                            {tag} 
                          </Badge>
                        ))}
                      </div>
                    )}
                  </CardContent>
                </Card>
              </div>
            </div>
          );
        })}
      </div>
    </section>
  );
}
